// GRADIENT COLOR
$lred: #ff856a;
$dred: #fe67a2;
$lgreen: #b2e055;
$dgreen: #5ac1c0;
$lpink: #fab17a;
$dpink: #ef7f97;
$lblue: #59e1cb;
$dblue: #32b8e6;
$lpurple: #dc8dff;
$dpurple: #7866f8;
$maingradient: linear-gradient(-220deg, lighten($red,15%), $red);
$redgradient: linear-gradient(-220deg, $lred, $dred);
$greengradient: linear-gradient(-220deg, $lgreen, $dgreen);
$pinkgradient: linear-gradient(-220deg, $lpink, $dpink);
$bluegradient: linear-gradient(-220deg, $lblue, $dblue);
$purplegradient: linear-gradient(-220deg, $lpurple, $dpurple);
