.bannerwrap {
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
		display: none; }
	.collection-title {
		@include fs(30rem);
		font-weight: 300;
		text-transform: uppercase;
		line-height: 1 !important; }
	.collection-name {
		@include fs(45rem);
		font-weight: 900;
		text-transform: uppercase;
		color: $white;
		border-bottom: 3px solid $black;
		padding: 10px 0;
		line-height: 1 !important; }
	.collection-year {
		@include fs(150rem);
		font-weight: 900;
		color: $main;
		line-height: 100px !important;
		@include media-breakpoint-down(md) {
			display: none; } } }
.homepage {
	.bannerwrap {
		@include media-breakpoint-up(lg) {
			min-height: 100vh; } } }
.block-thuongHieu {
	margin: 0 0 40px;
	figure {
		> a {
			display: block;
			overflow: hidden;
			@include pos(rel,null);
			&:after {
				content: '';
				background: transparent;
				@include pos(abs,0 0 null 0);
				bottom: 0;
				z-index: 9; }
			img {
				@include transition(all 0.5s); } }
		figcaption {
			@include pos(abs, 0 0 null 0);
			bottom: 0;
			@include flexbox;
			flex-wrap: wrap;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			@include transition(all 0.5s);
			@include opacity(0);
			&:before {
				content: '';
				background: rgba(255,255,255,0.7);
				@include pos(abs, 0 0 null 0);
				bottom: 0; }
			h3 {
				@include fs(20rem);
				font-weight: 600;
				line-height: 1.4;
				margin: 0 0 10px;
				padding: 0 0 7px;
				@include pos(rel,null);
				color: darken($white, 70%);
				&:after {
					content: '';
					@include pos(abs, null 0 0 0);
					width: 100%;
					height: 3px;
					background-color: darken($white, 70%); } }
			.price {
				@include fs(18rem);
				font-weight: 600;
				line-height: 1.4;
				color: lighten($red, 5%);
				@include pos(rel,null); } }
		&:hover, &:focus {
			> a {
				img {
					@include transform(scale(1.1,1.1)); } }
			figcaption {
				@include opacity(1); } } } }
.bannerPage {
	text-align: center; }
@-webkit-keyframes hvr-pulse {
	25% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1); }
	75% {
		-webkit-transform: scale(0.9);
		transform: scale(0.9); } }
@keyframes hvr-pulse {
	25% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1); }
	75% {
		-webkit-transform: scale(0.9);
		transform: scale(0.9); } }
.brands-categories {
	padding: 20px 0;
	background-color: darken($white, 5%);
	.nav {
		text-align: center;
		a {
			@include fs(20rem);
			font-weight: 500;
			line-height: 1;
			color: darken($white, 50%);
			display: block;
			background: $red;
			color: $white;
			padding: 15px;
			@include bor-rad(3px);
			&:hover, &:focus {
				color: $white;
				background: $red; }
			@include media-breakpoint-up(md) {
				display: inline-block; }
			+ a {
				margin: 15px 0 0;
				@include media-breakpoint-up(md) {
					margin: 0 0 0 20px; } }
			&.hvr-pulse.active {
				-webkit-animation-name: hvr-pulse;
				animation-name: hvr-pulse;
				-webkit-animation-duration: 1s;
				animation-duration: 1s;
				-webkit-animation-timing-function: linear;
				animation-timing-function: linear;
				-webkit-animation-iteration-count: infinite;
				animation-iteration-count: infinite; } } } }
.brightness {
	@include transition(all 0.5s);
	filter: brightness(0.9);
	-webkit-filter: brightness(0.9);
	-moz-filter: brightness(0.9);
	-o-filter: brightness(0.9);
	-ms-filter: brightness(0.9);
	&:hover, &:focus {
		filter: brightness(1.1);
		-webkit-filter: brightness(1.1);
		-moz-filter: brightness(1.1);
		-o-filter: brightness(1.1);
		-ms-filter: brightness(1.1); } }

.special-banner {
	margin-bottom: 30px;
	img {
		width: 100%; } }

.shopbanner {
	img {
		width: 100%; } }
