	label {
		display: none; }
	.rcRefreshImage {
		display: inline-block!important;
		vertical-align: middle;
		position: relative;
		color: transparent;
		width: 30px;
		height: 30px;
		&:after {
			content: '\f021';
			font-family: "FontAwesome";
			font-size: 18px;
			color: $black;
			position: absolute;
			top: 50%;
			right: 0;
			left: 0;
			@include transform(translate(0, -50%)); } }
	#ctl00_mainContent_ctl03_captcha_ctl01 {
		float: left;
		+ p {
			display: table-cell;
			padding: 7px 0;
			input {
				width: 100%; } } } }

