.radio-button {
	input {
		display: none;
		+ label {
			@include pos(rel,null);
			width: auto;
			padding: 0;
			padding-left: 20px;
			float: none;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				background: none;
				@include bor-rad(50px);
				border: 1px solid darken($white,40%);
				@include pos(abs, 0 null null 0);
				@include transform(translateY(3px)); }
			&:after {
				content: '';
				display: block;
				width: 10px;
				height: 10px;
				background: none;
				@include bor-rad(50px);
				border: 1px solid $white;
				@include pos(abs, 2px null null 2px);
				@include transform(translateY(3px));
				background: $lblue;
				opacity: 0;
				@include transition(all,0.3s); } }

		&:checked {
			+ label {
				&:before {
					border-color: $lblue; }
				&:after {
					opacity: 1; } } } } }

