.product-detail-page {
	.col-main {
		width: 100%; }
	.pages {
		display: none; }
	.block-prdNewsWrap {
		display: block; } }
.block-prdNewsWrap {
	display: none; }
@keyframes prnew {
	0% {
		background: $red; }
	50% {
		background: $blue; }
	100% {
		background: $red; } }
@keyframes prsale {
	0% {
		background: $dred; }
	50% {
		background: $red; }
	100% {
		background: $dred; } }
.col-left {
	width: 280px;
	padding: 0px 15px;
	// margin-top: 20px
	@include media-breakpoint-down(lg) {
		width: 230px; }
	@include media-breakpoint-down(md) {
		width: 0;
		margin: 0; } }
.col-main {
	width: calc(100% - 280px);
	padding: 0px 15px;
	// border-left: 1px solid darken($white,10%)
	// margin-top: 20px
	@include media-breakpoint-down(lg) {
		width: calc(100% - 230px); }
	@include media-breakpoint-down(md) {
		width: 100%;
		border-left: none; } }

.bannerads {
	margin-bottom: 30px;
	overflow: hidden;
	@include media-breakpoint-down(sm) {
		margin-bottom: 10px; }
	.slick-prev,.slick-next {
		@include pos(abs,50% null null null);
		@include transform(translateY(-50%));
		@include fs(0);
		z-index: 5;
		&:before {
			content: ''; } }
	.slick-prev {
		left: 0;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: 0;
		&:before {
			content: '\f105'; } }
	a {
		display: block;
		img {
			width: 100%; } } }
.product-banner {
	margin-bottom: 10px !important;
	.row {
		margin: 0px -3px;
		.col-12 {
			padding: 0px 3px; } }
	.item {
		.img {
			display: block;
			img {
				width: 100%; } } }
	.slick-dots {
		text-align: center;
		li {
			display: inline-block;
			width: 10px;
			height: 10px;
			@include bor-rad(50px);
			background: darken($white,20%);
			+ li {
				margin-left: 10px; }
			&.slick-active {
				background: $red; }
			button {
				display: none; } } } }

.category-info {
	// background: darken($white,5%)
	// border-bottom: 1px solid $main
	// +bor-rad(5px)
	padding: 10px 0px;
	// margin-bottom: 30px
	.cate-name {
		font-weight: 700;
		text-transform: uppercase;
		@include fs(18rem);
		float: left;
		margin-bottom: 5px; }
	.amount {
		float: left;
		clear: left;
		color: darken($white,40%);
		@include fs(12rem); }
	.product-sort {
		float: right;
		padding-top: 4px;
		select {
			border: 1px solid darken($white,10%);
			// +bor-rad(3px)
			padding: 5px 10px;
			color: darken($white,60%); } } }

.product-list, .product-list2 {
	.col-custom {
		@include media-breakpoint-up(xl) {
			@include flex(0 0 20%); } }
	.product-col {
		padding: 0 15px;
		@include pos(rel,null);
		figure {
			@include transition(all,0.5s);
			.promotion {
				@include pos(abs, 10px null null null);
				color: $white;
				@include fs(12rem);
				padding: 1px 5px;
				@include bor-rad(3px);
				z-index: 5;
				&.new {
					left: 10px;
					background: $red;
					animation-name: prnew;
					animation-duration: 2s;
					animation-iteration-count: infinite;
					text-transform: uppercase; }
				&.sale {
					right: 10px;
					background: $dred;
					animation-name: prsale;
					animation-duration: 2s;
					animation-iteration-count: infinite;
					// &:before
 } }					/// content: '\f0e7'
			.product-img, picture {
				@include flexbox;
				justify-content: center;
				align-items: center;
				height: 250px;
				margin: 0px -15px;
				@include pos(rel,null);
				img {
					max-width: 90%;
					max-height: 90%;
					@include transition(all,0.5s); } }
			.product-des {
				@include pos(abs, 0 0 null 0);
				height: 250px;
				z-index: 10;
				padding: 15px;
				overflow: hidden;
				background: color-rgba($white,0.9);
				opacity: 0;
				visibility: hidden;
				@include transition(all,0.5s);
				.content {
					@include fs(13rem);
					color: darken($white,60%);
					display: -webkit-box;
					height: 0.8125rem*1.5*10;
					line-height: 1.6;
					-webkit-line-clamp: 10;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					margin-bottom: 8px; }
				.btn-compare {
					@include fs(13rem);
					color: $red;
					&:hover {
						color: darken($red,20%); } } }
			figcaption {
				@include flexbox;
				flex-wrap: wrap;
				border-top: 1px solid darken($white,10%);
				padding: 10px 0;
				@include pos(rel,null);
				.product-name {
					width: 100%;
					display: -webkit-box;
					@include fs(15rem);
					font-weight: 500;
					text-transform: uppercase;
					height: 0.9375rem*1.4*1;
					line-height: 1.4;
					-webkit-line-clamp: 1;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					a {
						color: $main; } }
				.product-size {
					width: 100%;
					@include fs(12rem);
					color: darken($white,40%); }
				.product-color {
					width: 100%;
					margin-bottom: 5px;
					.color {
						display: inline-block;
						width: 10px;
						height: 10px;
						@include bor-rad(50px);
						+ .color {
							margin-left: 2px; } } }
				.product-price {
					width: calc(100% - 36px);
					@include flexbox;
					align-items: center;
					align-content: center;
					flex-wrap: wrap;
					.old {
						width: 100%;
						line-height: 1.2;
						@include fs(14rem);
						color: darken($white,40%);
						text-decoration: line-through; }
					.new {
						width: 100%;
						@include fs(16rem);
						font-weight: 700;
						color: $red;
						line-height: 1.2; } }

				.btn-wrap {
					@include pos(abs, null 0px 10px null);
					text-align: center; }
				.btn-buy {
					display: block;
					line-height: 34px;
					text-align: center;
					@include bor-rad(50px);
					color: $white;
					@include fs(12rem);
					background: $red;
					text-transform: uppercase;
					font-weight: 500;
					padding: 0px 20px;
					&:hover {
						background: linear-gradient(to right, darken($red,20%),$red);
						box-shadow: 0px 10px 20px -10px darken($red,20%); } } }
			&:hover {
				.product-img {}
				.product-des {
					visibility: visible;
					opacity: 1; }
				figcaption {
					.btn-buy {
						background: $red;
						&:hover {
							background: linear-gradient(-45deg, darken($red,20%),$red);
							box-shadow: 0 10px 20px -10px $red; } } } } } }
	.product-card {
		border-right: 1px solid darken($white,10%);
		border-bottom: 1px solid darken($white,10%);
		figure {} } }



