	.btn-closepopup, .ui-dialog-titlebar-close {
		@include pos(abs, -20px -20px null null);
		@include fs(42rem);
		color: $red;
		background: $white;
		width: 46px;
		height: 46px;
		line-height: 46px;
		text-align: center;
		background: #ff3333;
		@include bor-rad(50px);
		display: block;
		cursor: pointer;
		@include media-breakpoint-down(lg) {
			top: 0;
			right: 0; } }
	.caption {
		text-align: center;
		padding: 10px 0;
		color: $white;
		background: #ff3333;
		@include fs(27rem);
		text-transform: uppercase;
		@include media-breakpoint-down(sm) {
			@include fs(20rem); }
		@include media-breakpoint-down(xs) {
			@include fs(15rem);
			padding: 0px 30px; }
		.des {
			@include fs(16rem);
			@include media-breakpoint-down(sm) {
				@include fs(14rem); } } }
	.product {
		text-align: center;
		margin-bottom: 20px;
		figure {
			.product-img {
				margin-bottom: 10px;
				padding: 10px 0;
				width: 50%;
				float: left;
				text-align: center;
				@include media-breakpoint-down(xs) {
					width: 100%; }
				img {
					max-height: 300px; } }
			figcaption {
				display: table-cell;
				padding-left: 30px;
				text-align: left;
				padding-top: 40px;
				@include media-breakpoint-down(xs) {
					width: 100%;
					display: block;
					padding-left: 0; }
				.promotion {
					top: 40px; }
				.product-name {
					margin-bottom: 5px;
					@include fs(20rem);
					font-weight: 500;
					a {
						color: $black; } }
				.product-price {
					margin-bottom: 20%;
					.new {
						@include fs(25rem);
						color: $red;
						font-weight: 500; } }
				.button {
					.continueShoppingLink {
						display: block;
						margin-bottom: 10px;
						color: darken($white,40%);
						&:before {
							content: '\f104';
							margin-right: 5%; } }
					.btn-addtocart {
						display: inline-block;
						vertical-align: middle;
						line-height: 40px;
						@include bor-rad(50px);
						background: linear-gradient(to right, darken($red,20%), $red);
						@include fs(14rem);
						text-transform: uppercase;
						font-weight: 500;
						color: $white;
						padding: 0px 40px;
						overflow: hidden;
						text-align: center;
						&:hover {
							filter: brightness(120%);
							box-shadow: 0px 10px 20px -10px $red; } } } } } }
	.title {
		@include fs(18rem);
		font-weight: 500;
		color: $lblack;
		margin-bottom: 10px;
		text-transform: uppercase;
		@include media-breakpoint-down(md) {
			@include fs(16rem); } }
	.shipping-info {
		padding-top: 20px;
		margin-bottom: 20px;
		.form-group {
			label {
				display: none; }
			input, select, textarea {
				width: 100%;
				padding: 10px 20px;
				@include bor-rad(3px);
				border: 1px solid darken($white,20%); }
			select {
				padding: 9px 20px; } }
		.des {
			@include fs(13rem); } }
	.minicart {
		margin-top: 30px;
		.carttitle {
			background: none;
			border: none;
			@include bor-rad(0);
			border-bottom: 1px solid $black;
			padding: 0;
			span {
				display: inline-block;
				margin-bottom: -2px;
				border-bottom: 3px solid $red;
				padding: 5px 0; } }
		.btn-edit {
			top: 7px;
			right: 0; }
		.product-group {
			max-height: 182px;
			overflow-x: hidden;
			overflow-y: auto;
			border-bottom: 1px solid darken($white,10%); } }
	.cart-info {
		padding-top: 20px;
		margin-bottom: 20px;
		.title {
			span {
				display: inline-block;
				color: darken($white,40%);
				margin-left: 10px; } }
		.radio-button {
			input {
				display: none;
				+ label {
					@include pos(rel,null);
					width: auto;
					padding: 0;
					padding-left: 20px;
					float: none;
					&:before {
						content: '';
						display: block;
						width: 14px;
						height: 14px;
						background: none;
						@include bor-rad(50px);
						border: 1px solid darken($white,40%);
						@include pos(abs, 0 null null 0);
						@include transform(translateY(3px)); }
					&:after {
						content: '';
						display: block;
						width: 10px;
						height: 10px;
						background: none;
						@include bor-rad(50px);
						border: 1px solid $white;
						@include pos(abs, 2px null null 2px);
						@include transform(translateY(3px));
						background: $lblue;
						opacity: 0;
						@include transition(all,0.3s); } }

				&:checked {
					+ label {
						&:before {
							border-color: $lblue; }
						&:after {
							opacity: 1; } } } } }
		.table {
			.info-group {
				padding: 5px 0;
				color: darken($white,40%);
				.label {
					float: left; }
				.value {
					float: right; }
				&:last-child {
					color: $main;
					padding: 15px 0;
					border-top: 1px solid darken($white,40%);
					border-bottom: 1px solid darken($white,40%); } } }
		.slidewrap {
			@include pos(rel,null);
			margin-bottom: 20px;
			.prev, .next {
				@include fs(28rem);
				@include pos(abs, 50% null null null);
				@include transform(translateY(-50%));
				color: $black;
				@include transition(all,0.3s);
				z-index: 1;
				cursor: pointer;
				&:hover {
					color: $red; } }
			.prev {
				left: 0; }
			.next {
				right: 0; } }
		.cart-slide {
			.item {} }
		.btn-booking {
			border: none;
			margin-top: 20px;
			background: #ff3333;
			padding: 10px 0px;
			text-align: center;
			width: 100%;
			color: $white;
			text-transform: uppercase;
			font-weight: 500;
			box-shadow: 3px 3px 0 color-rgba($black,0.7); } }
	.combo-sale {
		border-top: 1px solid darken($white,40%);
		padding-top: 15px;
		.title {
			@include fs(16rem);
			text-transform: uppercase;
			padding-left: 20px; }
		.slidewrap {
			.next, .prev {
				top: -45px; }
			.prev {
				right: 60px; }
			.next {
				right: 15px; }
			.other-slide, .hot-in-modal {
				margin-bottom: 0; } } }
	.hot-product {
 } }		// display: none
.popup {
	width: 100%;
	height: 100%;
	display: none;
	@include pos(fix, 0 null null 0);
	background: rgba($black, 0.8);
	z-index: 1002;
	overflow-x: auto;
	overflow-y: auto;
	.popup-inner {
		max-width: 90%;
		width: 100%;
		@include pos(abs, 50% null null 50%);
		@include transform(translate(-50%,-50%));
		box-shadow: 0px 2px 6px rgba(0, 0, 0, 1);
		background: $white;
		@include media-breakpoint-up(sm) {
			max-width: 90%;
			width: 90%; }
		@include media-breakpoint-up(md) {
			max-width: 500px; }
		@include media-breakpoint-up(lg) {
			max-width: 600px; }
		@include media-breakpoint-up(xl) {
			max-width: 700px; }
		.popup-head {
			margin: 0;
			padding: 0;
			display: block;
			@include pos(rel,null);
			.popup-close {
				@include pos(abs, -15px -15px null null);
				@include fs(28rem);
				width: 45px;
				height: 45px;
				line-height: 40px;
				text-align: center;
				z-index: 9;
				color: $white;
				background: rgba($black, 0.8);
				@include bor-rad(50%); } }
		.popup-content {
			height: 500px;
			overflow-y: auto;
			-webkit-overflow-scrolling: touch;
			padding: 20px;
			@include media-breakpoint-up(lg) {
				height: 600px; }
			.loading-ajax {
				@include flexbox;
				justify-content: center;
				align-items: center;
				height: 400px;
				@include media-breakpoint-up(lg) {
					height: 500px; } } } } }
.view360_wrap {
	@include pos(rel,null);
	ul {
		li {
			img {
				object-fit: contain;
				max-height: 400px;
				@include media-breakpoint-up(lg) {
					max-height: 500px; } } } } }

