html {
	overflow: auto; }

.alert {
	display: block; }


.input-number {
	@include pos(rel,null);
	width: 90px;
	.btn-spin {
		@include pos(abs,0 null null null);
		width: 30px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		cursor: pointer;
		&.btn-dec {
			left: 0; }
		&.btn-inc {
			right: 0; } }
	input {
		width: 100%;
		border: 1px solid darken($white,10%);
		@include bor-rad(5px);
		@include fs(17rem);
		font-weight: 700;
		color: $black;
		text-align: center;
		padding: 4px 10px; } }
.cartpage {
	main {
		padding-top: 40px; }
	header, .product-page {
 }		// display: none
	footer {
		.footer-top {
			display: none; }
		.copyright {
			background: darken($white,10%);
			text-align: center;
			padding: 0;
			color: darken($white,60%);
			.content {
				font-weight: 500; }
			a {
				color: $main;
				&:hover {
					color: $red; } } } }
	label.error {
		@include pos(abs, null 10px 10px null);
		@include fs(0);
		line-height: 1;
		&:before {
			content: '\f071';
			font-family: 'fontawesome';
			@include fs(16rem);
			color: $red; } } }
.carttitle {
	@include fs(18rem);
	color: $main;
	margin-bottom: 20px;
	background: darken($white,5%);
	border: 1px solid darken($white,10%);
	@include bor-rad(3px);
	padding: 10px;
	// &:after
	// 	content: ''
	// 	display: block
	// 	width: 50px
	// 	height: 2px
	// 	background: $red
	// 	margin-top: 5px
	strong {
		color: $red; } }
.cart-page {
	background: $white;
	margin-bottom: 30px;
	@include media-breakpoint-down(md) {
		margin: 30px 0; }
	.carttitle {
		&:before {
			content: '\f290';
			font-family: 'fontawesome';
			margin-right: 5px; } }
	.cartnotice {
		margin: 0 0 20px;
		border: 1px solid lighten($red,30%);
		background: lighten($red,45%);
		color: $red;
		padding: 10px 30px 10px 15px;
		@include pos(rel,null);
		@include fs(14rem);
		a {
			color: $blue; } }
	.removenotice {
		@include pos(abs, 7px 15px null null);
		cursor: pointer;
		@include fs(16rem);
		&:before {
			content: '\f057';
			font-family: 'fontawesome'; } }
	.carttable {
		width: 100%;
		border: 1px solid $main;
		@include bor-rad(5px);
		margin-bottom: 10px;
		tr {
			@include media-breakpoint-down(sm) {
				display: block;
				border-bottom: 1px solid darken($white,10%);
				padding: 15px;
				&:after {
					content: '';
					display: table;
					width: 100%;
					height: 0;
					clear: both; } } }
		thead {
			@include media-breakpoint-down(sm) {
				display: none; } }
		th {
			background: $main;
			color: $white;
			font-weight: normal;
			padding: 10px 15px;
			text-transform: uppercase;
			@include fs(15rem);
			font-weight: 300;
			vertical-align: middle;
			@include media-breakpoint-down(sm) {
				display: none; } }
		td {
			border-bottom: 1px solid darken($white,10%);
			padding: 15px;
			@include media-breakpoint-down(sm) {
				display: block;
				float: left;
				width: 100%;
				border: none;
				padding: 0; } }
		th, td {
			text-align: center;
			&:first-child {
				text-align: left; }
			&:last-child {
				text-align: right; } }
		tr {
			&:last-child {
				td {
					border: none; } } }
		.product {
			@include media-breakpoint-down(sm) {
				width: 50%; }
			@include media-breakpoint-down(xs) {
				width: 100%; }
			.productimg {
				display: block;
				width: 80px;
				height: 80px;
				float: left;
				border: 1px solid darken($white,10%);
				@include media-breakpoint-down(sm) {
					width: 90px;
					height: 90px; }
				img {
					max-height: 78px;
					max-width: 78px; } }
			.productdetail {
				float: left;
				padding: 3px 10px;
				.productname {
					@include fs(15rem);
					font-weight: bold;
					// margin-bottom: 5px
					a {
						color: $main; } }
				.productsize {
					@include fs(14rem);
					color: darken($white,60%);
 }					// margin-bottom: 5px
				.removeproduct {
					a {
						color: darken($white,40%);
						&:before {
							content: '\f057';
							font-family: 'fontawesome';
							margin-right: 5px;
							color: lighten($red,10%); } }
					&:hover {
						color: lighten($red,10%); } } } }
		.price {
			width: 130px;
			@include fs(15rem);
			@include media-breakpoint-down(sm) {
				width: 50%;
				text-align: right; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-top: 10px;
				text-align: left; }
			.new {
				font-weight: bold;
				@include media-breakpoint-down(xs) {
					display: inline-block; } }
			.old {
				color: darken($white,40%);
				text-decoration: line-through;
				@include fs(14rem);
				@include media-breakpoint-down(xs) {
					display: inline-block;
					margin-left: 10px; } }
			.sale {
				color: lighten($red,10%);
				@include media-breakpoint-down(xs) {
					display: inline-block;
					margin-left: 10px; } } }
		.amount {
			width: 130px;
			@include media-breakpoint-down(sm) {
				width: auto;
				text-align: right;
				clear: left;
				margin-top: 10px; }
			.input-number {
				display: inline-block; } }
		.total {
			width: 130px;
			.amount {
				@include fs(15rem);
				font-weight: bold;
				@include media-breakpoint-down(sm) {
					width: auto;
					float: right;
					clear: right;
					margin-top: 10px;
					@include fs(18rem);
					color: $black; } } } }
	.totalamount {
		text-align: right;
		@include fs(16rem);
		strong {
			color: $red;
			@include fs(24rem); } }
	.payment-des {
		text-align: right;
		font-style: italic;
		@include fs(14rem); } }
.btn-back {
	display: block;
	background: darken($white,20%);
	color: $main;
	@include fs(14rem);
	padding: 10px 30px;
	float: left;
	margin: 15px 0 0;
	text-align: center;
	@include bor-rad(5px);
	overflow: hidden;
	@include media-breakpoint-down(xs) {
		width: 100%; }
	&:hover {
		background: darken($white,60%);
		color: $white; }
	&:before {
		content: '\f177';
		font-family: 'fontawesome';
		margin-right: 15px; } }
.btn-continue {
	display: block;
	background: $lblack;
	color: $white;
	@include fs(14rem);
	padding: 10px 30px;
	float: right;
	margin: 15px 0 0;
	text-align: center;
	@include bor-rad(5px);
	overflow: hidden;
	@include media-breakpoint-down(xs) {
		width: 100%; }
	&:hover {
		color: $white; }
	&:after {
		content: '\f178';
		font-family: 'fontawesome';
		margin-left: 15px; }
	&:before {
		background: $red; } }

