// CART HEADER

.cartheader {
	background: $black;
	.sitelogo {
		margin: 15px 0;
		float: left;
		a {
			display: block;
			img {
				height: 70px; } } }
	.hotline {
		float: right;
		padding: 5px;
		@include bor-rad(50px);
		border: 2px solid color-rgba($white,0.5);
		margin: 12px 0 5px;
		@include media-breakpoint-down(xs) {
			display: none; }
		.fa {
			display: block;
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			@include fs(24rem);
			@include bor-rad(50px);
			float: left;
			background: $white; }
		.text {
			float: left;
			padding: 0 10px;
			line-height: 1.3;
			color: color-rgba($white,0.8);
			a {
				display: block;
				font-weight: bold;
				color: $red;
				&:hover {
					color: $white; } } } }
	.cartdes {
		float: right;
		clear: right;
		@include fs(14rem);
		color: darken($white,40%);
		@include media-breakpoint-down(xs) {
			display: none; }
		a {
			color: $white;
			&:hover {
				color: $red; } } } }

// CART FOOTER

.cartfooter {
	background: darken($white,10%);
	text-align: center;
	padding: 15px 0;
	color: darken($white,40%); }

// CART STEP

.cartstep {
	background: darken($white,5%);
	margin: 30px 0;
	overflow: hidden;
	// border: 1px solid #BDBDBD
	.step {
		width: 25%;
		float: left;
		padding: 10px 25px;
		@include pos(rel,null);
		@include media-breakpoint-down(md) {
			width: 100%;
			display: none; }
		&:before {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: 28px solid transparent;
			border-bottom: 28px solid transparent;
			border-right: 0 solid transparent;
			border-left: 17px solid $white;
			@include pos(abs, -3px -2px null null);
			@include transform(translateX(15px));
			z-index: 10; }
		&:after {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: 25px solid transparent;
			border-bottom: 25px solid transparent;
			border-right: 0 solid transparent;
			border-left: 15px solid darken($white,5%);
			@include pos(abs, 0px 0px null null);
			@include transform(translateX(15px));
			z-index: 11; }
		&:last-child {
			&:before, &:after {
				display: none; } }
		.number {
			display: inline-block;
			vertical-align: middle;
			width: 30px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			background: darken($white,15%);
			color: $main;
			@include bor-rad(50px);
			@include media-breakpoint-down(md); }
		.name {
			display: inline-block;
			vertical-align: middle;
			padding-left: 10px;
			@include media-breakpoint-down(md); }
		&.active {
			background: darken($white,20%);
			@include media-breakpoint-down(md) {
				display: block; }
			&:after {
				border-left-color: darken($white,20%); }
			.number {
				background: $red;
				color: $white;
				font-weight: bold; }
			.name {
				color: $main; }
			&.finish {
				@include media-breakpoint-down(md) {
					display: none; } } }
		&.finish {
			.number {
				background: $green;
				// background: darken($white,60%)
				color: $white;
				@include fs(0);
				&:before {
					content: '\f00c';
					font-family: 'fontawesome';
					@include fs(15rem); } } } } }

// CART LOGIN
.cart-login {
	border: 1px solid darken($white,10%);
	padding: 30px 0;
	margin-bottom: 50px;
	@include media-breakpoint-down(md) {
		margin-bottom: 30px; }
	@include media-breakpoint-down(xs) {
		padding: 15px; }
	a {
		color: $black;
		&:hover {
			color: $red; } }
	.login-form {
		width: 400px;
		margin: 0 auto;
		@include media-breakpoint-down(xs) {
			width: 100%; }
		.module-title {
			@include fs(14rem);
			font-weight: 500;
			margin-bottom: 10px; }
		.form-group {
			margin-bottom: 10px; }
		input {
			width: 100%;
			border: 1px solid darken($white,20%);
			padding: 10px 15px;
			@include fs(14rem);
			@include transition(all,0.3s);
			&:hover, &:active, &:focus {
				border-color: darken($white,40%); } }
		.select-group {
			margin-bottom: 10px;
			input[type="radio"] {
				display: none;
				+ label {
					padding: 5px 0 5px 20px;
					@include pos(rel,null);
					color: darken($white,60%);
					@include transition(all,0.3s);
					&:hover {
						color: $main; }
					&:before {
						content: '';
						display: block;
						width: 14px;
						height: 14px;
						@include bor-rad(50px);
						@include pos(abs, 8px null null 0);
						border: 1px solid darken($white,40%); }
					&:after {
						content: '';
						display: block;
						width: 14px;
						height: 14px;
						border: 4px solid $red;
						@include pos(abs, 8px null null 0);
						@include bor-rad(50px);
						opacity: 0;
						@include transition(all,0.3s); } }
				&:checked {
					+ label {
						color: $main;
						&:after {
							opacity: 1; } } } } }

		.forget-password {
			display: block;
			@include fs(14rem);
			color: $main;
			margin-bottom: 0px;
			&:hover {
				color: $red; } }
		.btn-continue {
			display: block;
			float: none;
			text-align: center;
			background: $black;
			border: none;
			&:hover {
				background: $red; } } } }
.minicart {
	@include pos(rel,null);
	margin-bottom: 50px;
	.btn-edit {
		@include pos(abs, 12px 15px null null);
		@include fs(15rem);
		color: $red;
		&:hover {
			color: $main; } }
	.carttable {
		border: 1px solid darken($white,10%);
		.group {
			padding: 10px 15px;
			display: table;
			width: 100%;
			border-bottom: 1px solid darken($white,10%);
			&:last-child {
				border: none; }
			.product {
				display: table-cell;
				vertical-align: top;
				&:after {
					content: '';
					display: table;
					width: 100%;
					clear: both; }
				.productimg {
					width: 70px;
					height: 70px;
					float: left;
					border: 1px solid darken($white,10%);
					img {
						max-width: 68px;
						max-height: 68px; } }
				.productdetail {
					display: table-cell;
					padding-left: 10px;
					line-height: 1.5;
					.productname {
						font-weight: bold;
						line-height: 1.5;
						@include fs(14rem);
						a {
							color: $main;
							display: block;
							&:hover {
								color: $red; } } } } }
			.amount {
				display: table-cell;
				vertical-align: top;
				text-align: right;
				line-height: 1.5;
				@include fs(14rem);
				width: 100px;
				.price {
					font-weight: bold; } }
			.label {
				display: table-cell;
				text-align: left; }
			.value {
				display: table-cell;
				text-align: right;
				font-weight: bold; } } } }

.help {
	display: inline-block;
	margin-left: 10px;
	color: darken($white,40%);
	@include fs(18rem);
	@include pos(rel,null);
	.help-tip {
		@include pos(abs, 50% 0 null null);
		min-width: 140px;
		@include fs(12rem);
		padding: 5px 10px;
		background: $lblack;
		@include transform(translateX(calc(100% + 10px))translateY(-50%));
		opacity: 0;
		@include transition(all,0.3s);
		margin-top: 10px;
		&:before {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border: 7px solid;
			border-color: transparent $lblack transparent transparent;
			@include pos(abs, 50% null null 1px);
			@include transform(translateY(-50%)translate(-100%));
			z-index: 10; } }
	&:hover {
		color: $red;
		.help-tip {
			opacity: 1;
			margin-top: 0;
			color: darken($white,40%);
			&:hover {
				color: $white; } } } }

.receive-address, .shipping-info, .export-bill, .coupon, .shipping-kind, .payment-info {
	margin-bottom: 20px;
	.title {
		background: darken($white,5%);
		color: $main;
		font-weight: 500;
		border: 1px solid darken($white,10%);
		padding: 12px 15px 12px 40px;
		@include pos(rel,null);
		@include bor-rad(3px);
		line-height: 1.5;
		label {
			font-weight: 500; } }

	.cart-form {
		padding: 20px 0;
		.module-title {
			@include fs(14rem);
			margin-bottom: 15px; }
		.des {
			@include fs(14rem);
			color: darken($white,40%); }
		.form-group {
			display: table;
			width: 100%;
			margin-bottom: 15px;
			.des {
				@include fs(13rem);
				color: darken($white,40%); }
			.label, .input, .hr {
				display: table-cell;
				vertical-align: middle;
				@include media-breakpoint-down(xs) {
					display: block; } }
			.label {
				width: 200px;
				@include media-breakpoint-down(sm) {
					width: 150px; }
				@include media-breakpoint-down(xs) {
					width: 100%;
					margin-bottom: 5px; } }
			.input {
				input, textarea, select {
					width: 100%;
					border: 1px solid darken($white,20%);
					padding: 10px 15px;
					@include fs(14rem);
					@include transition(all,0.3s);
					&::-webkit-input-placeholder {
						opacity: 0.3; }
					&:hover, &:active, &:focus {
						border-color: darken($white,40%); } }
				select {
					padding: 9px 15px; }
				&.phone {
					input {
						display: inline-block;
						vertical-align: middle;
						width: 50%;
						@include media-breakpoint-down(xs) {
