					padding-left: 25px;
					@include pos(rel,null);
					&:hover {
						color: $black; }
					&:before {
						content: '';
						display: block;
						width: 16px;
						height: 16px;
						@include bor-rad(50px);
						border: 1px solid darken($white,20%);
						@include pos(abs, 2px null null 0); }
					&:after {
						content: '';
						display: block;
						width: 10px;
						height: 10px;
						@include bor-rad(50px);
						background: $red;
						@include pos(abs, 5px null null 3px);
						z-index: 1;
						@include transition(all,0.5s);
						opacity: 0; } }
				&:checked {
					+ label {
						&:after {
							opacity: 1; } } } } }

		.check-group {
			margin-bottom: 10px;
			input {
				display: none;
				+ label {
					color: $main;
					padding-left: 25px;
					@include pos(rel,null);
					&:hover {
						color: $black; }
					&:before {
						content: '';
						display: block;
						width: 16px;
						height: 16px;
						@include bor-rad(3px);
						border: 1px solid darken($white,20%);
						@include pos(abs, 2px null null 0); }
					&:after {
						content: '\f00c';
						font-family: 'fontawesome';
						display: block;
						width: 16px;
						height: 16px;
						line-height: 14px;
						text-align: center;
						@include bor-rad(3px);
						color: $red;
						border: 1px solid darken($white,20%);
						@include pos(abs, 2px null null 0);
						z-index: 1;
						@include transition(all,0.5s);
						opacity: 0; } }
				&:checked {
					+ label {
						&:after {
							opacity: 1; } } } } }
		.user-info {
