.payment-info {
	margin-bottom: 30px;
	@include media-breakpoint-down(md) {
		margin-bottom: 10px; }
	.title {
		&:before {
			content: '\f21d';
			font-family: 'fontawesome';
			@include pos(abs, 7px null null 15px);
			@include fs(18rem); } }
	.cart-form {
		.payment-method {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; }
			.select-group {
				display: block;
				padding: 0;
				input {
					+ label {
						display: block;
						width: 100%;
						@include fs(0);
						&:before, &:after {
							top: 50%;
							@include transform(translateY(-50%)); }
						.method-info {
							display: block;
							// width: calc(100% - 30px)
							width: 100%;
							border: 1px solid darken($white,10%);
							@include bor-rad(3px);
							@include transition(all,0.3s);
							&:after {
								content: '';
								display: table;
								width: 100%;
								height: 0;
								clear: both; }
							.method-img {
								display: flex;
								justify-content: center;
								align-items: center;
								width: 85px;
								height: 85px;
