								border-right: 1px solid darken($white,10%);
								padding: 10px 0;
								@include transition(all,0.3s);
								@include media-breakpoint-down(xs) {
									width: 100%;
									border-right: none;
									border-bottom: 1px solid darken($white,10%); } }
							.method-detail {
								float: left;
								padding: 10px;
								width: calc(100% - 85px);
								@include media-breakpoint-down(xs) {
									width: 100%;
									text-align: center; }
								.method-name {
									@include fs(14rem);
									font-weight: 500; }
								.method-des {
									@include fs(13rem);
									color: darken($white,40%); } } } }
					&:checked {
						+ label {
							.method-info {
								border-color: darken($white,60%);
								.method-img {
									border-color: darken($white,60%); } } } } } } } } }

