.new-product {
	padding-top: 40px;
	@include media-breakpoint-down(md) {
 }		// padding-top: 60px
	.headtitle {
		@include fs(120rem);
		font-weight: 900;
		color: $black;
		// +transform(rotateZ(90deg))
		// line-height: 80px
		line-height: 1;
		writing-mode: vertical-lr;
		@include media-breakpoint-down(md) {
			writing-mode: horizontal-tb; }
		@include media-breakpoint-down(xs) {
			@include fs(50rem); } }
	.nopadding {
		padding: 0;
		@include media-breakpoint-down(xs) {
			padding: 0px 15px; } }
	.product-slide {
		.item {
			a {
				display: block;
				img {
					width: 100%; } } } }
	#slide2 {
		margin-top: -230px;
		@include media-breakpoint-down(lg) {
			margin-top: -100px; }
		@include media-breakpoint-down(md) {
			margin-top: -150px; }
		@include media-breakpoint-down(sm) {
			margin-top: 0; } }
	.slide {
		@include media-breakpoint-down(sm) {
			order: 1; } }
	.text {
		@include media-breakpoint-down(sm) {
			order: 2; } }
	.slide-caption {
		padding: 100px 0 0;
		color: $black;
		@include media-breakpoint-down(lg) {
			padding-top: 70px; }
		@include media-breakpoint-down(sm) {
			padding-top: 30px;
			padding-bottom: 60px; }
		@include media-breakpoint-down(xs) {
			padding-bottom: 30px; }
		&:after {
			content: '';
			display: block;
			width: 140px;
			height: 3px;
			background: darken($white,10%); }
		.collection-title {
			@include fs(16rem);
			text-transform: uppercase;
			// font-weight: 500
			margin-bottom: 5px;
			letter-spacing: 3px;
			margin-left: 50px;
			@include media-breakpoint-down(lg) {
				margin-left: 30px; }
			@include media-breakpoint-down(md) {
				@include fs(14rem); } }
		.collection-name {
			@include fs(42rem);
			text-transform: uppercase;
			margin-left: 50px;
			margin-bottom: 25px;
			@include media-breakpoint-down(lg) {
				margin-left: 30px; }
			@include media-breakpoint-down(md) {
				@include fs(28rem); } }
		&.slide-caption2 {
			text-align: right;
			&:after {
				float: right; }
			.collection-title {
				margin-left: 0;
				margin-right: 50px;
				@include media-breakpoint-down(lg) {
					margin-right: 30px; } }
			.collection-name {
				margin-right: 50px;
				@include media-breakpoint-down(lg) {
					margin-right: 30px; } } } }
	.product-row {
		margin-bottom: 40px;
		@include media-breakpoint-down(md) {
			padding-top: 0; }
		@include media-breakpoint-down(xs) {
			margin: 0px -15px; } }
	.intro-caption {
		background: $black;
		color: $white;
		padding: 30px 60px;
		@include pos(abs, null null 0 80px);
		width: 60%;
		@include media-breakpoint-down(lg) {
			width: 80%; }
		@include media-breakpoint-down(md) {
			width: 90%;
			left: unset;
			right: 0;
			padding: 15px; }
		@include media-breakpoint-down(sm) {
			@include pos(static,null);
			width: 80%;
			margin: 30px auto; }
		@include media-breakpoint-down(xs) {
			width: 100%;
			margin-top: 0; }
		.title {
			@include fs(28rem);
			text-transform: uppercase;
			font-weight: 500;
			line-height: 1.2;
			margin-bottom: 10px;
			@include media-breakpoint-down(md) {
				@include fs(24rem); } }
		.des {
			@include fs(14rem); } } }

.home-intro {
	background: #f3f5f8;
	padding: 20px 0;
	text-align: center;
	@include media-breakpoint-down(xs) {
		display: none; }
	.title {
		@include fs(25rem);
		text-transform: uppercase;
		font-weight: 300;
		// margin-bottom: 10px
		@include media-breakpoint-down(xs) {
			margin-bottom: 0; } }
	.des {
		font-weight: 300;
		@include fs(16rem); }
	.link {
		display: inline-block;
		text-transform: uppercase;
		padding: 7px 20px;
		border: 1px solid darken($yellow,20%);
		margin-top: 10px;
		color: darken($white,60%);
		&:hover {
			color: darken($yellow,20%);
			background: $white;
			border-color: $white; } } }
.block-instagramMobile {
	@include media-breakpoint-down(sm) {
		display: none; } }
.home-product {
	padding: 20px 0 0;
	.brand-slick {}
	.brand-slide {
		// border-top: 1px dashed $main
		// border-bottom: 1px dashed $main
		// margin: 0 -30px
		margin-bottom: 20px;
		.item {
			padding: 30px 0;
			// height: 230px
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			.brand-logo {
				width: 110px;
				height: 110px;
				display: flex;
				justify-content: center;
				align-items: center;
				@include transition(all,0.5s);
				border: 1px solid darken($white,10%);
				@include transform(rotateZ(45deg));
				background: $white;
				@include media-breakpoint-down(lg) {
					width: 150px;
					height: 150px; }
				@include media-breakpoint-down(md) {
					width: 130px;
					height: 130px; }
				@include media-breakpoint-down(sm) {
					width: 100px;
					height: 100px; }
				@include media-breakpoint-down(xs) {
					width: 70px;
					height: 70px; }
				// box-shadow: 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12), 0 2px 4px -1px rgba(0,0,0,.2)
				img {
					max-width: 90%;
					max-height: 90%;
					@include transform(rotateZ(-45deg)); } } } }
	.bigproduct-slide {
		// margin: 0 -50px
		.slick-list {
			// padding: 0 50px
			@include media-breakpoint-down(xs) {
 } } }				// padding: 50px 50px 100px
	.product-wrap {
		// overflow: hidden
		.product {
			width: calc(100% - 200px);
			float: left;
			@include media-breakpoint-down(sm) {
				width: 100%; }
			figure {
				width: 100%;
				@include pos(rel,null);
				.product-bg {
					@include pos(abs, 0 null 0 0);
					display: flex;
					justify-content: center;
					align-items: center;
					right: 0;
					text-align: center; }
				.product-img {
					@include pos(rel,null);
					z-index: 1;
					padding: 80px;
					text-align: center;
					@include media-breakpoint-up(lg) {
						padding: 80px 80px 160px; }
					@include media-breakpoint-down(xs) {
						padding: 0px;
						margin-bottom: 30px; }
					a {
						display: block; }
					img {
						display: initial;
						height: 600px;
						@include media-breakpoint-down(md) {
							height: auto; }
						@include media-breakpoint-down(xs) {
							height: auto; } } }
				figcaption {
					@include pos(rel,null);
					z-index: 1;
					color: $black;
					font-weight: 500;
					@include fs(28rem);
					@include media-breakpoint-down(xs) {
						@include fs(24rem); } } } }
		.product-list {
			width: 200px;
			@include pos(rel,null);
			z-index: 1;
			float: left;
			box-shadow: 0 0 30px color-rgba($black, 0.3);
			margin-bottom: 10px;
			background: $white;
			// height: 900px
			// overflow: auto
			@include media-breakpoint-down(sm) {
				width: 100%;
				box-shadow: none;
				margin-top: 30px; }
			.product-col {
				width: 200px;
				overflow: hidden;
				float: right;
				background: linear-gradient($white 90%,color-rgba($black,0.1));
				border: 1px solid darken($white,10%);
				@include transition(all,0.5s, ease-in-out);
				@include pos(rel,null);
				@include media-breakpoint-down(sm) {
					background: $white;
					border-bottom: 1px solid darken($white,10%);
					width: 100%;
					margin-bottom: 10px; }
				@include media-breakpoint-down(md) {
					&:nth-child(3) {
						display: none; } }
				@include media-breakpoint-down(xs) {
					display: none;
					&:nth-child(1) {
						display: block; } }
				&:last-child {
					background: none; }
				figure {
					width: 600px;
					@include transition(all,0.5s);
					@include media-breakpoint-down(sm) {
						background: none;
						width: 100%; }
					.product-img {
						display: flex;
						align-items: center;
						justify-content: center;
						width: 199px;
						// height: 300px
						float: left;
						padding: 30px 0;
						@include media-breakpoint-down(sm) {
							width: 100%;
							// height: 200px
							padding: 10px 0; }
						@include media-breakpoint-down(xs) {
							height: auto;
							padding: 20px 0; }
						picture {
							width: 100%; }
						img {
							// max-height: 90%
							width: 100%;
							max-width: 100%;
							@include media-breakpoint-down(xs) {
 } } }								// max-height: 80%
					figcaption {
						width: 399px;
						float: left;
						padding: 20px;
						color: $main;
						@include media-breakpoint-down(md) {
 }							// padding-bottom: 80px
						@include media-breakpoint-down(sm) {
							width: 100%;
							color: $main; }
						.product-category {
							color: $main; }
						@include media-breakpoint-down(sm) {
							color: $main; }
						.product-name {
							font-weight: 500;
							@include fs(16rem);
							margin-bottom: 15px;
							a {
								color: $yellow;
								&:hover {
									color: lighten($blue,40%); } } }
						.product-des {
							ul {
								li {
									@include pos(rel,null);
									padding-left: 20px;
									&:before {
										content: '\f111';
										font-family: 'fontawesome';
										@include fs(4rem);
										color: $main;
										@include pos(abs, 7px null null 0); } } } }
						.button {
							@include pos(abs, null 20px -50px null);
							@include transition(all,0.5s);
							@include media-breakpoint-down(sm) {
								bottom: 15px; }
							a {
								display: inline-block;
								width: 36px;
								height: 36px;
								@include bor-rad(50%);
								background: $blue;
								text-align: center;
								line-height: 36px;
								color: $white;
								&:hover {
									background: $yellow; }
								+ a {
									margin-left: 10px; } } } } }
				&:hover {
					@include media-breakpoint-up(md) {
						width: 600px;
						figure {
							background: $white;
							border: 1px solid darken($white,10%);
							figcaption {
								.button {
									bottom: 30px; } } } } } } } } }

