.home-instagram {
	padding-bottom: 20px;
	@include media-breakpoint-down(md) {
		display: none; }
	.instapopup-overlay {
		@include pos(fix, 0 null 0 0);
		right: 0;
		background: color-rgba($black,0.5);
		z-index: 115;
		display: none; }
	.album-list {
		margin: 0 -5px;
		@include media-breakpoint-down(xs) {
			padding: 0px 10px; } }
	.album-col {
		width: 33.33%;
		&:nth-child(2) {
			width: 66.66%; }
		@include media-breakpoint-down(md) {
			width: 100%;
			&:last-child {
				width: 100%;
				.album {
					width: 25%; } } }
		@include media-breakpoint-down(xs) {
			width: 100%;
			&:last-child {
				.album {
					width: 50%; } } }
		&:after {
			content: '';
			display: table;
			width: 100%;
			clear: both; }
		.album {
			width: 25%;
			height: 210px;
			float: left;
			padding: 0 5px;
			margin-bottom: 10px;
			overflow: hidden;
			@include pos(rel,null);
			@include media-breakpoint-down(lg) {
				height: 160px; }
			@include media-breakpoint-down(xs) {
				height: auto; }
			&.big {
				width: 100%;
				height: 430px;
				@include media-breakpoint-down(lg) {
					height: 330px; }
				@include media-breakpoint-down(md) {
					height: 660px; }
				@include media-breakpoint-down(xs) {
					height: auto; } }
			figure {
				.album-img {
					display: block;
					overflow: hidden;
					img {
						width: 100%;
						@include transition(all,0.5s); } }
				figcaption {
					@include transition(all,0.5s);
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: color-rgba($white,0.7);
					display: flex;
					justify-content: center;
					align-items: center;
					flex-wrap: wrap;
					text-align: center;
					opacity: 0;
					@include transform(scale3d(1.5,1.5,1));
					.fa {
						display: block;
						@include fs(48rem);
						margin-bottom: 20px;
						@include media-breakpoint-down(lg) {
							@include fs(38rem); } }
					.link {
						display: block;
						color: $main;
						text-transform: uppercase;
						padding: 10px 30px;
						border: 2px solid $main;
						cursor: pointer;
						@include media-breakpoint-down(lg) {
							@include fs(13rem);
							padding: 7px 20px; }
						&:hover {
							background: $main;
							color: $white; } } } }
			&:hover {
				figure {
					.album-img {
						img {
							@include transform(scale3d(1.2,1.2,1)); } }
					figcaption {
						opacity: 1;
						@include transform(scale3d(1,1,1)); } } } } } }

