.insta-popup {
	background: $white;
	padding: 30px;
	padding-bottom: 31px;
	@include pos(fix, 50% null null 50%);
	max-height: 90vh;
	z-index: 116;
	overflow: auto;
	@include transform(translate(-50%,-50%));
	@include media-breakpoint-down(lg) {
		width: 560px;
		max-height: 90vh; }
	@include media-breakpoint-down(xs) {
		width: 100%;
		padding: 20px; }
	.btn-closepopup {
		width: 16px;
		height: 16px;
		@include pos(abs, 10px 10px null null);
		cursor: pointer;
		@include media-breakpoint-down(xs) {
			top: 5px;
			right: 5px; }
		&:before {
			content: '';
			display: block;
			height: 2px;
			background: $black;
			@include pos(abs, 38% 0 null 0);
			@include transform(rotateZ(45deg)); }
		&:after {
			content: '';
			display: block;
			height: 2px;
			background: $black;
			@include pos(abs,null 0 49% 0);
			@include transform(rotateZ(-45deg)); }
		&:hover {
			&:before, &:after {
				background: $red; } } }
	.product-col {
		float: left;
		@include media-breakpoint-down(xs) {
			float: none;
			width: 100%; }
		figure {
			.product-img {
				display: block;
				margin-bottom: 25px;
				@include media-breakpoint-down(xs) {
					margin-bottom: 10px; }
				img {
					width: 380px;
					@include media-breakpoint-down(xs) {
						width: 100%; } } }
			figcaption {
				.author {
					.img {
						float: left; }
					.detail {
						float: left;
						color: $black;
						padding-left: 20px;
						.name, time {
							font-weight: 500;
							a {
								color: $main;
								&:hover {
									color: $red; } } } } } } } }
	.slide-col {
		width: 120px;
		padding: 0 20px;
		margin-left: 20px;
		float: left;
		@include media-breakpoint-down(lg) {
			margin-left: 0; }
		@include media-breakpoint-down(xs) {
			width: 100%;
			margin: 0;
			padding: 0;
			@include pos(rel,null); }
		.prev, .next {
			width: 100%;
			height: 30px;
			line-height: 30px;
			@include fs(24rem);
			text-align: center;
			cursor: pointer;
			display: none;
			@include media-breakpoint-down(xs) {
				width: auto;
				@include pos(abs, 50% null null null);
				@include transform(translateY(-50%) rotateZ(-90deg));
				z-index: 5; }
			&:hover {
				color: $red; } }
		.prev {
			@include media-breakpoint-down(xs) {
				left: -15px; } }
		.next {
			@include media-breakpoint-down(xs) {
				right: -15px; } }
		.ver-slide {
			max-height: 390px;
			overflow: hidden;
			.slick-list {
 } }				// height: 400px !important
		.item {
			margin-bottom: 15px;
			@include media-breakpoint-down(xs) {
				width: 50%;
				float: left; }
			.product {
				text-align: center;
				figure {
					.product-img {
						display: block;
						margin-bottom: 5px;
						text-align: center;
						img {
							// width: 100%
							display: initial; } }
					figcaption {
						.product-name {
							@include fs(12rem);
							font-weight: 500;
							text-align: center;
							margin-bottom: 5px; }
						.link {
							display: inline-block;
							@include fs(13rem);
							text-transform: uppercase;
							color: $white;
							padding: 5px 20px;
							background: $black; } } } } }
		.social {
			text-align: center;
			a {
				display: inline-block;
				@include fs(20rem);
				color: $black;
				+ a {
					margin-left: 10px; } } } } }
.slidewrap {
	.item {
		figure {
			a {
				img {
					max-width: 90%;
					max-height: 90%; } } } } }

