		right: 0;
		background: color-rgba($black,0.5);
		z-index: 115;
		display: none; }
	.title {
		@include fs(25rem);
		text-transform: uppercase;
		font-weight: 300;
		margin-bottom: 10px;
		text-align: center; }
	.des {
		font-weight: 300;
		@include fs(16rem);
		text-align: center; }
	.slidewrap {
		margin: 0 -15px; }
	.upcoming-slide {
		.slick-list {
			padding-top: 80px; }
		.slick-dots {
			text-align: center;
			@include pos(abs, 20px 0 null 0);
			li {
				display: inline-block;
				width: 20px;
				height: 20px;
				@include bor-rad(50%);
				border: 1px solid $black;
				@include transition(all,0.3s);
				cursor: pointer;
				+ li {
					margin-left: 20px; }
				&.slick-active {
					background: $black; }
				button {
					display: none; } } }
		.item {
			padding: 0 15px; }
		.upcoming-col {
			border: 3px solid darken($white,8%);
			border-top: 6px solid darken($white,8%);
			@include pos(rel,null);
			.upcoming-img {
				display: flex;
				width: 100%;
				height: 300px;
				justify-content: center;
				align-items: center; }
			figcaption {
				text-align: center;
				.countdown, .coundown {
					@include fs(25rem);
					padding: 0 10px;
					background: $white;
					color: $black;
					@include pos(abs, -20px null null 50%);
					@include transform(translateX(-50%)); }
				.upcoming-name, .start, time {
					text-transform: uppercase;
					color: $black;
					display: block;
					line-height: 1.5; }
				time {
					margin-bottom: 10px; }
				.link {
					display: inline-block;
					color: $white;
					text-transform: uppercase;
					background: $blue;
					@include fs(14rem);
					font-weight: 300;
					padding: 10px 30px;
					cursor: pointer;
					margin-bottom: 10px; } } } } }


.home-news {
	background: #f3f5f8;
	padding: 40px 0;
	text-align: center;
	.title {
		@include fs(25rem);
		text-transform: uppercase;
		font-weight: 300;
		margin-bottom: 10px; }
	.des {
		font-weight: 300;
		@include fs(16rem); }
	.link {
		display: inline-block;
		text-transform: uppercase;
		padding: 7px 20px;
		border: 1px solid darken($yellow,20%);
		margin-top: 20px;
		color: darken($white,60%);
		&:hover {
			color: darken($yellow,20%);
			background: $white;
			border-color: $white; } }
	.bricklayer {
		text-align: left;
		margin: 30px -15px 0;
		display: flex;
		flex-wrap: wrap;
		.bricklayer-column-sizer, .item {
			width: 33.33%;
			padding: 0 15px;
			@include media-breakpoint-down(md) {
				width: 50%; }
			@include media-breakpoint-down(sm) {
				width: 100%; } }
		.bricklayer-column {
			padding: 0 10px; } }
	.item {
		@include media-breakpoint-down(md) {
			&:nth-child(3) {
				display: none; } }
		@include media-breakpoint-down(xs) {
			&:nth-child(2) {
				display: none; } } }
	.news-col {
		margin-bottom: 20px;
		@include bor-rad(5px);
		background: $white;
		overflow: hidden;
		height: 100%;
		figure {
			.news-img {
				@include pos(rel,null);
				display: block;
				img {
					width: 100%; }
				.play {
					display: none;
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: url(../img/play.png) center center no-repeat; } }
			figcaption {
				padding: 15px 20px;
				.news-type {
					@include fs(12rem);
					font-weight: bold;
					margin-bottom: 10px;
					.fa {
						font-weight: normal;
						color: $orange;
						margin-right: 5px; } }
				.news-name {
					font-weight: bold;
					@include fs(18rem);
					margin-bottom: 10px;
					a {
						color: $green;
						&:hover {
							color: $orange; } } }
				.news-info {
					@include fs(11rem);
					margin-bottom: 10px;
					time {}
					.author {
						margin-left: 30px;
						color: $orange; }
					.cmt-amount {
						margin-left: 30px; } }
				.news-des {
					@include fs(12rem);
					color: darken($white,40%); } } } } }
.fb-likeAll {
	@include pos(fix, 350px 0 null null);
	background: darken($white, 30%);
	padding: 15px 10px; }

.home-product-list {
	.row {
		margin: 0px -5px; }
	.col-xs-12 {
		padding: 0px 5px;
		margin-bottom: 10px; }
	.product-col {
		height: 100%;
		border: 1px solid darken($white,10%);
