footer {
	.subscribe-section {
		background: $lblack;
		@include media-breakpoint-down(xs) {
			padding: 20px 0; }
		.subscribefrm {
			.module-title {
				@include fs(16rem);
				font-weight: 500;
				text-transform: uppercase;
				color: $white;
				float: left;
				line-height: 40px;
				margin: 15px 0;
				@include media-breakpoint-down(sm) {
					width: 100%;
					text-align: center; }
				@include media-breakpoint-down(xs) {
					@include fs(14rem);
					font-weight: 500;
					padding: 0;
					margin-bottom: 10px; } }
			.subscribe {
				width: calc(100% - 380px);
				float: left;
				@include bor-rad(50px);
				overflow: hidden;
				margin: 15px 0 15px 50px;
				@include pos(rel,null);
				@include media-breakpoint-down(lg) {
					width: calc(100% - 350px);
					margin-left: 20px; }
				@include media-breakpoint-down(sm) {
					width: 80%;
					float: none;
					margin: 15px auto; }
				@include media-breakpoint-down(xs) {
					width: 100%; }
				input {
					width: 100%;
					border: none;
					background: $white;
					padding: 0px 20px;
					line-height: 40px;
					@include media-breakpoint-down(xs) {
						line-height: 40px; } }
				.subscribebutton {
					background: $maingradient;
					color: $white;
					@include fs(16rem);
					font-weight: 500;
					text-transform: uppercase;
					padding: 0px 30px;
					line-height: 40px;
					@include bor-rad(0 50px 50px 0);
					vertical-align: middle;
					@include pos(abs, 0 0 0 null);
					@include media-breakpoint-down(xs) {
						line-height: 40px;
						@include fs(16rem);
						font-weight: 500; } } } }

		.social {
			padding: 15px 0;
			text-align: right;
			@include media-breakpoint-down(md) {
				text-align: center; }
			@include media-breakpoint-down(xs) {
				padding: 5px 0; }
			.label {
				@include fs(16rem);
				line-height: 40px;
				text-transform: uppercase;
				color: $white;
				display: inline-block;
				vertical-align: middle;
				margin-right: 10px;
				@include media-breakpoint-down(xs) {
					@include fs(13rem);
					font-weight: 500; } }
			a {
				display: inline-block;
				vertical-align: middle;
				width: 40px;
				height: 40px;
				background: $white;
				color: $main;
				@include fs(24rem);
				line-height: 40px;
				text-align: center;
				@include bor-rad(50px);
				margin-left: 10px;
				@include media-breakpoint-down(xs) {
					width: 36px;
					height: 36px;
					line-height: 36px;
					@include fs(18rem); } } } }
	.footer-category {
		@include media-breakpoint-down(xs) {
			padding: 30px 0; }
		article {
			border-left: 1px solid darken($white,20%);
			@include media-breakpoint-down(md) {
				border: none; }
			&:first-child {
				border: none; }
			&:nth-child(3) {
				@include media-breakpoint-down(md) {
					border: none; } } }
		.category-col {
			height: 100%;
			padding: 60px 0;
			@include media-breakpoint-down(md) {
				padding: 30px 0; }
			@include media-breakpoint-down(xs) {
				text-align: center;
				padding: 10px 0; }
			figure {
				padding: 0 10px;
				.category-img {
					display: flex;
					height: 55px;
					align-items: center;
					margin-bottom: 15px;
					@include media-breakpoint-down(xs) {
						justify-content: center; }
					img {
						width: auto;
						height: auto;
 } }						// max-height: 50px
				figcaption {
					.category-name {
						@include fs(20rem);
						font-weight: 500;
						text-transform: uppercase;
						margin-bottom: 15px;
						a {
							color: $black;
							&:hover {
								color: $red; } } }
					.category-des {
						@include fs(14rem);
						font-weight: 300;
						@include media-breakpoint-down(xs) {
							padding: 0px 30px; } } } } } }
	.footer-bottom {
		background: $lblack;
		@include flexbox;
		flex-wrap: wrap;
		padding: 0px 15px;
		padding-top: 40px;
		.title {
			@include fs(15rem);
			font-weight: 500;
			text-transform: uppercase;
			color: $white;
			margin-bottom: 15px;
			@include media-breakpoint-down(xs) {
				text-align: center; } }
		.footer-col {
			margin-bottom: 40px;
			@include media-breakpoint-down(xs) {
				text-align: center; } }
		.footer-link {
			ul {
				li {
					@include fs(13rem);
					font-weight: 500;
					margin-bottom: 5px;
					a {
						color: $white;
						&:hover {
							color: $orange; } } } } }
		.hotline {
			@include fs(13rem);
			color: $white;
			.phone {
				display: block;
				@include fs(30rem);
				font-weight: 500;
				color: $red;
				@include media-breakpoint-down(lg) {
					@include fs(28rem); }
				&:hover {
					color: $orange; } } }
		.payment {
			margin-bottom: 15px; }
		.partner {
			img {
				display: inline-block;
				vertical-align: middle;
				margin-right: 15px;
				margin-bottom: 10px; } } } }
.btn-up, .btn-down {
	display: none;
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	background: $main;
	color: $white;
	cursor: pointer;
	@include pos(fix, null 30px 50px null);
	z-index: 100;
	&:hover {
		background: $red; } }
.btn-down {
	right: 65px;
	a {
		color: $white; } }
