body {
	padding-top: 130px;
	@include media-breakpoint-down(lg) {
		padding-top: 50px; } }
header {
	background: $white;
	width: 100%;
	display: none;
	@include pos(fix, 0 0 null 0);
	z-index: 1000;
	@include media-breakpoint-up(xl) {
		display: block;

		&.minimal {
			box-shadow: 0 10px 20px -10px color-rgba($black,0.1); }
		.header-top {
			background: darken($white,3%);
			.material-icons {
				@include fs(16rem); }
			.hotline {
				float: left;
				line-height: 30px;
				color: darken($white,60%);
				@include fs(13rem);
				a {
					color: $black;
					font-weight: 600;
					&:hover {
						color: $red; } } }
			.colright {
				float: right; }
			.check-tool {
				float: left;
				+ .check-tool {
					margin-left: 30px; }
				.dropdown {
					&.show {
						.dropdown-toggle {
							color: $red; } }
					.dropdown-toggle {
						line-height: 30px;
						color: darken($white,60%);
						@include fs(13rem);
						&:after {
							display: none; }
						&:hover {
							color: $red; } }
					.check-form {
						padding: 10px 15px;
						.form-group {
							margin: 0 0 10px;
							label {
								@include fs(13rem);
								margin: 0; }
							input {
								line-height: 30px;
								@include fs(14rem);
								border: none;
								padding: 0px;
								margin: 0;
								border-bottom: 2px solid darken($white,20%);
								&:hover, &:active, &:focus {
									border-color: $black; } } }
						input[type="submit"] {
							@include mainbutton($lred, $red);
							border: none;
							@include fs(13rem);
							line-height: 32px;
							padding: 0px 20px; } } } } }
		.header-main {
			.sitelogo {
				@include flexbox;
				align-items: center;
				align-content: center;
				height: 60px;
				float: left;
				a {
					display: block; }
				img {
					height: 40px;
 } }					// filter: invert(100%) grayscale(100%) brightness(400%)
			.search {
				float: left;
				margin-left: 40px;
				@include flexbox;
				height: 60px;
				align-items: center;
				align-content: center;
				.searchwrap {}
				.searchbox {
					width: 350px;
					@include flexbox;
					align-items: center;
					align-content: center;
					@include bor-rad(5px);
					border: 1px solid darken($white,10%);
					// background: darken($white,3%)
					overflow: hidden;
					input {
						width: calc(100% - 40px);
						line-height: 36px;
						padding: 0;
						margin: 0;
						border: none;
						background: none;
						@include fs(13rem);
						padding: 0px 15px;
						color: $black;
						&::-webkit-input-placeholder {
							opacity: 0.5; } }
					.searchbutton {
						width: 40px;
						text-align: center;
						@include fs(13rem);
						height: 36px;
						line-height: 36px;
						background: none;
						appearance: none;
						cursor: pointer;
						background: darken($white,5%);
						// border-left: 1px solid darken($white,10%)
						color: darken($white,60%);
						&:hover {
							color: $red; } } } }
			.colright {
				float: right; }
			.sitefeature {
				float: left;
				@include flexbox;
				width: 400px;
				height: 60px;
				align-items: center;
				align-content: center;
				.feature {
					width: 33.33%;
					figure {
						@include flexbox;
						.icon {
							width: 32px;
							filter: brightness(0%); }
						figcaption {
							width: calc(100% - 32px);
							@include flexbox;
							align-items: center;
							align-content: center;
							padding: 0 10px;
							.des {
								line-height: 1.3;
								@include fs(12rem);
								color: darken($white,40%); } } } } }
			.login {
				float: left;
				margin-left: 20px;
				@include flexbox;
				height: 60px;
				align-items: center;
				align-content: center;
				padding-left: 36px;
				@include pos(rel,null);
				width: 100px;
				&:before {
					content: 'person';
					font-family: 'Material Icons';
					@include fs(36rem);
					line-height: 1;
					@include pos(abs, 50% null null 0);
					@include transform(translateY(-50%)); }
				ul {
					@include flexbox;
					flex-wrap: wrap;
					li {
						width: 100%;
						line-height: 1.3;
						&:first-child {
							order: 2;
							a {
								@include fs(11rem); } }
						&:last-child {
							order: 1;
							a {
								@include fs(13rem);
								font-weight: 600; } } } }
				a {
					@include flexbox;
					color: $black;
					&:hover {
						color: $red; } } }
			.cart {
				float: left;
				margin-left: 20px;
				@include flexbox;
				height: 60px;
				align-items: center;
				align-content: center;
				.cart-toggle {
					@include flexbox;
					align-items: center;
					align-content: center;
					color: $black;
					border: 2px solid color-rgba($black,0.3);
					padding: 5px;
					@include bor-rad(5px);
					cursor: pointer;
					@include transition(all,0.3s);
					&:hover {
						color: $red;
						.cart-amount {
							a {
								color: $red;
								// +media-breakpoint-down(lg)
 } } }								// 	color: $white
					.material-icons {
						@include fs(18rem);
						margin-right: 10px; }
					.cart-amount {
						@include fs(13rem);
						a {
							color: $black; }
						span {
							display: inline-block;
							@include bor-rad(3px);
							color: $white;
							font-weight: 600;
							padding: 0px 5px;
							text-align: center;
							min-width: 22px;
							background: $maingradient; } } } } }
		.header-menu {}
		.menu {
			ul.menulink {
				@include flexbox;
				width: 100%;
				@include pos(rel,null);
				> li {
					// width: calc(100% / 9)
					flex: auto;
					@include transition(all,0.3s);
					> a {
						@include flexbox;
						padding: 0px 10px;
						align-content: center;
						align-items: center;
						justify-content: center;
						@include fs(14rem);
						height: 40px;
						line-height: 20px;
						color: darken($white,40%);
						@include pos(rel,null);
						&:after {
							content: '';
							display: block;
							@include pos(abs, 0 null 0 0);
							right: 0;
							opacity: 0;
							// height: 3px
							background: $maingradient;
							@include transition(all,0.5s); }
						.icon {
							@include flexbox;
							justify-content: center;
							align-items: center;
							align-content: center;
							width: 20px;
							height: 20px;
							@include pos(rel,null);
							z-index: 1;
							img {
								max-width: 100%;
								max-height: 100%;
								// filter: invert(100%)
								opacity: 0.6; }
							.material-icons {
								@include fs(20rem);
								line-height: 20px; } }
						.text {
							// width: calc(100% - 30px)
							padding-left: 5px;
							@include flexbox;
							line-height: 20px;
							align-items: center;
							align-content: center;
							text-transform: uppercase;
							font-weight: 500;
							@include pos(rel,null);
							z-index: 1; } }
					// &:hover
					// 	> a
					// 		color: $red
					// 		.icon
					// 			img
					// 				opacity: 1
					&:hover,&.active {
						> a {
							color: $white;
							.icon {
								img {
									opacity: 1;
									filter: invert(100%); } }
							.text {
								font-weight: 500; }
							&:after {
								opacity: 1; } }
						// &.news, &.brand
						// 	> a
 }						// 		color: $white
					&.news {
						background: linear-gradient(-220deg,$amber,$orange);
						> a {
							color: $white; }
						// 	.icon
						// 		filter: invert(100%)
						&:hover, &.active {}
						// 	filter: brightness(110%)
						// a
						//color: $white
						//// &:after
 }						// 			display: none
					&.brand {
						background: linear-gradient(-220deg,$lgreen,$dgreen);
						> a {
							color: $white; }
						// 	.icon
						// 		filter: invert(100%)
						&:hover, &.active {
							> a {
								color: $white;
 } }								// &:after
 } } } } } }						// 			display: none
