.landingpage {
	header {
		@include pos(abs, 0 0 null 0);
		border-bottom: 1px solid darken($white,10%); } }
.landing-header {
	// +pos(abs, 131px 0 null 0)
	z-index: 1000;
	background: $white;
	&.active {
		box-shadow: 0px 10px 20px -10px color-rgba($black,0.1); }
	.sitelogo, .landing-product-name {
		height: 60px;
		@include flexbox;
		align-items: center;
		align-content: center;
		float: left;
		a {
			display: block;
			@include fs(18rem);
			text-transform: uppercase;
			font-weight: 700;
			color: $main;
			&:hover {
				color: $red; } }
		img {
			height: 30px; } }
	.col-right {
		float: right;
		.btn-buy {
			display: block;
			padding: 0px 60px;
			@include mainbutton($lred,$red);
			text-transform: uppercase;
			margin: 10px 0;
			box-shadow: none;
			&:hover {
				filter: brightness(120%);
				box-shadow: 0px 10px 20px -10px color-rgba($red,1); } } }
	.landing-menu {
		background: darken($white,3%);
		ul.menu-link {
			@include flexbox;
			> li {
				+ li {
					margin-left: 40px; }
				> a {
					@include fs(13rem);
					font-weight: 700;
					text-transform: uppercase;
					color: darken($white,40%);
					@include pos(rel,null);
					@include flexbox;
					align-items: center;
					align-content: center;
					height: 40px;
					&:before {
						content: '';
						display: block;
						height: 3px;
						background: linear-gradient(to right, $lred, $red);
						@include pos(abs, null 0% -1px 0%);
						opacity: 0;
						@include transition(all,0.5s); }
					&:hover, &.mPS2id-highlight {
						color: $main;
						&:before {
							// right: 0
							// left: 0
							opacity: 1; } } } } } } }

.product-section {
	padding: 120px 0 60px;
	@include pos(rel,null);
	.landing-bg {
		@include pos(abs, 0 0 null 0);
		bottom: 0;
		overflow: hidden;
		opacity: 0.6;
		&:before {
			content: '';
			display: block;
			width: 1600px;
			height: 1000px;
			background: radial-gradient($white, color-rgba($white,0) 70%);
			opacity: 1;
			@include pos(abs, 0 null null 50%);
			@include transform(translate(-50%,-30%)); }
		&:after {
			content: '';
			@include pos(abs, null 0 0 0);
			height: 200px;
			background: linear-gradient(color-rgba($white,0),color-rgba($white,1)); }
		img {
			width: 100%; } }
	.landing-intro {
		@include pos(rel,null);
		z-index: 5;
		.product-id {
			color: $main;
			@include fs(14rem);
			text-transform: uppercase;
			margin-bottom: 5px; }
		.product-name {
			@include fs(38rem);
			font-weight: 700;
			text-transform: uppercase;
			margin-bottom: 5px; }
		.product-brand {
			text-transform: uppercase;
			font-weight: 300;
			@include fs(14rem);
			margin-bottom: 20px;
			a {
				color: $main;
				font-weight: normal; } }
		.product-price {
			margin-bottom: 20px;
			@include flexbox;
			.new {
				@include fs(36rem);
				// font-weight: 500
				color: $red;
				line-height: 1; }
			.old {
				@include fs(16rem);
				color: darken($white,40%);
				text-decoration: line-through;
				font-weight: 300;
				line-height: 1;
				margin-top: 15px;
				margin-left: 20px; } }
		.product-option {
			.option-group {
				margin-bottom: 10px;
				.label {
					@include fs(13rem);
					font-weight: 700;
					text-transform: uppercase;
					margin-bottom: 5px; }
				ul {
					@include flexbox;
					flex-wrap: wrap;
					li {
						margin-right: 10px;
						a {
							display: block;
							&:hover {
								.color, .size {
									border-color: darken($white,40%); } }
							&.active {
								.color, .size {
									border-color: $red; } }
							&.disabled {
								.color, .size {
									opacity: 0.3;
									border-color: darken($white,10%);
									&:hover {
										border-color: darken($white,10%); } } } } } }
				.color {
					border: 2px solid darken($white,20%);
					@include bor-rad(50px);
					padding: 2px;
					@include transition(all,0.5s);
					span {
						display: block;
						width: 20px;
						height: 20px;
						@include bor-rad(50px); } }
				.size {
					padding: 0px 10px;
					@include bor-rad(3px);
					color: $main;
					font-weight: 500;
					@include fs(15rem);
					border: 2px solid darken($white,20%);
					@include transition(all,0.5s); } } }
		.product-gift {
			@include bor-rad(5px);
			border: 1px solid darken($white,10%);
			background: $white;
			margin-bottom: 20px;
			@include pos(rel,null);
			&:before {
				content: '';
				display: block;
				width: 10px;
				height: 10px;
				background: darken($white,2%);
				border: 1px solid transparent;
				border-color: darken($white,10%) darken($white,10%) transparent transparent;
				@include transform(rotateZ(-45deg));
				@include pos(abs, -5px null null 15px); }
			.title {
				@include fs(15rem);
				font-weight: 500;
				padding: 10px 15px;
				background: darken($white,2%);
				border-bottom: 1px solid darken($white,10%);
				@include bor-rad(5px 5px 0 0);
 }				// text-transform: uppercase
			.notice {
				font-style: italic;
				@include fs(13rem);
				color: $red;
				margin: 0px 15px 10px;
				&:before {
					content: '*'; } }
			.gift-list {
				padding: 15px;
				li {
					padding-left: 20px;
					@include pos(rel,null);
					margin-bottom: 5px;
					@include fs(15rem);
					&:last-child {
						margin-bottom: 0; }
					&:before {
						content: '\f058';
						@include pos(abs, 0 null null 0);
						color: $green; }
					a {
						color: darken($white,40%);
						&:hover {
							color: $red; } } } } }
		.btn-wrap {
			margin-bottom: 20px;
			.btn-buy {
				display: inline-block;
				@include fs(18rem);
				padding: 0 60px;
				line-height: 40px;
				color: $white;
				background: linear-gradient(135deg, lighten($red,20%), $red);
				box-shadow: 0px 10px 20px -10px $red;
				@include bor-rad(50px);
				text-transform: uppercase;
				font-weight: 300;
				&:hover {
					filter: brightness(120%); } } }
		.product-overview {
			color: $black; } }
	.landing-media {
		@include pos(rel,null);
		z-index: 5;
		.landing-product-img {
			@include flexbox;
			justify-content: center;
			align-items: center;
			height: 500px;
			margin-bottom: 30px;
			img {
				max-width: 100%;
				max-height: 100%; } }
		.landing-slide {
			@include flexbox;
			justify-content: center;
			a {
				@include flexbox;
				justify-content: center;
				align-items: center;
				width: 60px;
				height: 60px;
				@include bor-rad(5px);
				background: $white;
				border: 1px solid darken($white,10%);
				+ a {
					margin-left: 10px; }
				img {
					max-width: 90%;
					max-height: 90%;
					background: $white; } } } } }
.landing-video {
	.video {
		figure {
			@include pos(rel,null);
			.video-img {
				@include pos(rel,null);
				display: block;
				overflow: hidden;
				&:before {
					content: '\f144';
					@include pos(abs, 0 null 0 0);
					right: 0;
					z-index: 2;
					@include flexbox;
					justify-content: center;
					align-items: center;
					@include fs(140rem);
					color: color-rgba($black,0.5);
					@include transform(scale3d(0.8,0.8,1));
					@include transition(all,0.3s); }
				&:after {
					content: '';
					@include pos(abs, 0 null 0 0);
					right: 0;
					z-index: 1;
					background: color-rgba($white,0.3);
					@include transition(all,0.3s); }
				img {
					@include transition(all,0.3s);
					width: 100%; } }
			figcaption {
				.video-name {
					@include fs(24rem);
					font-weight: 700;
					@include pos(abs, 0 0 null 0);
					z-index: 5;
					// text-shadow: 0px 1px 5px color-rgba($black,0.3)
					a {
						display: block;
						color: $main;
						padding: 20px; } } }
			&:hover {
				.video-img {
					img {
						@include transform(scale3d(1.1,1.1,1)); }
					&:before {
						color: color-rgba($white,0.9);
						@include transform(scale3d(1,1,1)); }
					&:after {
						background: color-rgba($black,0.1); } }
				figcaption {
					.video-name {
						text-shadow: 0px 1px 5px color-rgba($black,0.3);
						a {
							color: $white; } } } } } } }
.landing-title {
	@include fs(24rem);
	font-weight: 500;
	text-transform: uppercase;
	color: $black;
	margin-bottom: 30px; }
.content-section {
	padding: 60px 0;
	background: $white;
	.content {
		@include fs(16rem);
		// text-align: center
		color: darken($white,60%);
		max-width: 1000px;
		margin: 0 auto 30px;
		* {
			font-weight: 300;
			line-height: 1.8; }
		h5 {
			@include fs(20rem);
			color: $black;
			strong {
				font-weight: 400; } } }
	.btn-wrap {
		margin-bottom: 50px; }
	.btn-download {
		color: lighten($red,20%);
		font-weight: 300;
		@include fs(18rem);
		&:hover {
			color: $red; }
		&:after {
			content: '\f019';
			margin-left: 5px; } }
	.product-img1 {
		@include flexbox;
		justify-content: center;
		height: 600px;
		margin-bottom: 40px;
		img {
			max-width: 100%;
			max-height: 100%; } } }
.specifi-section {
	padding: 60px 0;
	background: darken($white,2%);
	.table {
		max-width: 800px;
		margin: 0 auto;
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		overflow: hidden;
		background: $white;
		tr {
			&:first-child {
				td {
					border-top: none; } } }
		td {
			@include fs(15rem);
			&:first-child {
				font-weight: 700;
				border-right: 1px solid darken($white,10%); } } } }
.feature-section {
	padding: 60px 0;
	background: url(../img/landing/bn2.jpg) center center no-repeat;
	background-size: cover;
	background-attachment: fixed;
	overflow: hidden;
	@include pos(rel,null);
	.landing-title {
 }		// color: $white
	&:before {
		content: '';
		display: block;
		@include pos(abs, 0 null 0 0);
		right: 0;
		background: darken($white,10%);
		opacity: 0.9; }
	.container {
		@include pos(rel,null);
		z-index: 1; }
	.feature-list {
		@include pos(rel,null);
		.product-img2 {
			@include flexbox;
			justify-content: center;
			align-items: center;
			height: 450px;
			@include pos(abs, null null 0px 50%);
			@include transform(translate(-50%, 0));
			img {
				max-width: 100%;
				max-height: 100%; } }
		.feature {
			margin-bottom: 30px;
			figure {
				@include flexbox;
				.icon {
					width: 80px;
					height: 80px;
					@include flexbox;
					justify-content: center;
					align-items: center;
					background: $white;
					@include bor-rad(10px);
					box-shadow: 0px 10px 20px -10px color-rgba($black,0.3);
					img {
						max-width: 60%;
						max-height: 60%;
						filter: invert(100%); } }
				figcaption {
					width: calc(100% - 60px);
					padding-left: 15px;
					@include fs(13rem);
 } } } } }					// color: $white
.review-section {
	padding: 60px 0;
	.fb-comment {
		@include flexbox;
		justify-content: center; } }

.fancybox-image-wrap {
	background: $white;
	img {
		background: $white; } }

